import React, {useRef} from 'react';
import Title from '../Title';
import { useForm } from "react-hook-form";
import {useDispatch} from 'react-redux';
import { setToken } from '../../slices/tokenSlice.js';
import {Link, useNavigate} from 'react-router-dom';

const RegistrContainer = ({serverPort}) => {
    const dispatch = useDispatch()
    const navigate = useNavigate();

    const {
        register,
        handleSubmit,
        watch,
        formState: { errors }
      } = useForm();

    const password = useRef({});
    password.current = watch("password", "");
  
    const onSubmit = (data) => {
  
      console.log(",  :");
      console.log(data);
  
      tryToSendAddAttemptRequest(serverPort, {login:data.login, password:data.password}).then(
      (registrationResult) => {
          console.log("    :" + registrationResult);
          if(registrationResult === "  "){
            sendLoginRequest(serverPort, data.login, data.password).then((token) => {
                console.log("   : " + token);
                dispatch(setToken(token));
                navigate('/main', {replace: true});
              }
              ).catch(() => {
                console.log("   , ,    !");
                alert("   , ,    !");
              });
          } else{
              alert(registrationResult);
          }
        }
        ).catch(() => {

        console.log("   !");
          alert("   !");
        }
      );
    };

    return (
        <form className="register_box container" onSubmit={handleSubmit(onSubmit)}>
          <Title text=''/>
          <label></label>
          <input placeholder=':    '
              {...register("login", {required: true, pattern: /^[A-Za-z0-9]+$/i, })} />
          {errors?.login?.type === "pattern" && ( <p className='error'>   </p>)}
          {errors?.login?.type === "minLength" && <p className='error'>   </p>}
          {errors?.login?.type === "required" && <p className='error'>   </p>}
    
          <label></label>
          <input type="password" placeholder=':    '
          {...register("password", { required: true, pattern: /^[A-Za-z0-9]+$/i, minLength: 8,})} />
          {errors?.password?.type === "pattern" && (<p className='error'>   </p>)}
          {errors?.password?.type === "minLength" && <p className='error'>   </p>}
          {errors?.password?.type === "required" && <p className='error'>   </p>}
    
    
          <label> </label>
          <input type="password" placeholder=' '
          {...register("repeatePassword", {
               required: true,
               pattern: /^[A-Za-z0-9]+$/i,
                minLength: 8,
                validate: value => value === password.current})} />
          {errors?.repeatePassword?.type === "pattern" && (<p className='error'>   </p>)}
          {errors?.repeatePassword?.type === "minLength" && <p className='error'>   </p>}
          {errors?.repeatePassword?.type === "required" && <p className='error'>   </p>}
          {errors?.repeatePassword?.type === "validate" && <p className='error'>  </p>}
    
          <input type="submit" value="" className='btn-block btn' />

            <Link className='swich_link' to="/"> ?</Link>
        </form>
    );
};

export default RegistrContainer;

let tryToSendAddAttemptRequest = async (port, data) => {
    console.log(port);
    let url = "http://localhost:"+ port +"/auth/register";
    console.log("Sending POST request to url: " + url + ". With body: " + JSON.stringify(data));
    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      mode: 'cors',
      body: JSON.stringify(data),
    });
    return response.text();
}

let sendLoginRequest = async (port, login, password) => {
    let url = "http://localhost:"+ port +"/auth/login?" + new URLSearchParams({"login":login, "password":password});
    console.log("  GET  URL:" + url);
    const response = await fetch(url, {
      method: 'GET',
      mode: 'cors',
    });
  
    let json = await response.json();
    console.log(json);
    return json.token;
  }
  
